	module.exports = {"data":{"wordpressPost":{"title":"BMJ joins the &#8216;open&#8217; debate","content":"<p><em>BMJ Open is the first BMJ Group journal to use fully open peer review</em></p>\n<p>Revealing to authors who has peer reviewed their article has helped to make the process fairer and increase the credit for reviewers, according to a recent editorial published by the <em>BMJ</em>.</p>\n<p><em>BMJ </em>Deputy Editor, Trish Groves presents her perspectives on the advantages of transparency in peer review using the <em>BMJ – </em>with more than 10 years’ experience in open peer review – as an example of success.  The article explores alternate models of journal peer review and references the findings of a recently published randomised controlled trial which suggested that ‘telling peer reviewers that their signed reviews might be available in the public domain on the BMJ’s website, had no important effect on review quality’.</p>\n<p>Read the full article by Dr Trish Groves <a title=\"Link to BMJ article\" href=\"http://www.bmj.com/content/341/bmj.c6424.full\" target=\"_blank\">here</a>:</p>\n<p>In a counter-debate published in the <em>BMJ</em>, Karim Khan, Editor in Chief of the <em>BJSM </em>argues that open peer review may dissuade reviewers from offering a true judgement of a paper, and in some cases, enable a jealous rival to sabotage the review process. He takes a closer look at closed peer review, where reviewers can opt for anonymity.</p>\n<p>Karim Khan’s article is available in full <a title=\"Open BMJ article\" href=\"http://www.bmj.com/content/341/bmj.c6425.full\" target=\"_blank\">here</a>:<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby for BMJ blog sites"}}},"pathContext":{"id":"2a48e4f1-8bb2-5d1d-814c-7cfe1893cdc1"}}

